@import '../../styles/colors.scss';

.Sidebar {
  height: 100vh;
  width: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $color-dark-blue-100;
  justify-content: space-between;

  .header {
    width: 100px;
    height: 212px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $color-dark-blue-200;
  }

  .nav {
    padding-top: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-items: center;
    flex-direction: column;
    >:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  .navElement {
    cursor: pointer;
    z-index: 1;
    &:hover * {
      opacity: 1;
    }
    text-decoration: none;
    color: white;
  }

  .active * {
    opacity: 1;
    color: $color-blue;
  }

  .logout {
    z-index: 1;
  }

  .cubes {
    position: absolute;
    bottom: 0;
  }
}
